import { socialMedia, projectNames } from "~/config";
export default {
    data() {
        return {
            socialMedia: null,
            projectNames: null,
        };
    },
    props: {
        title: {
            type: String,
            require: true,
        },
        content: {
            type: String,
            require: true,
        },
        posts: {
            type: Array,
        },
    },
    created() {
        this.socialMedia = socialMedia;
        this.projectNames = projectNames;
    }
};
